        color: #616161;

       .widgets{
           display: flex;
           padding: 20px;
           gap: 20px;
       }
       .charts{
        padding: 5px 20px;
        display: flex   ;
       }
       .listContainer{
        -webkit-box-shadow: 2px 4px 10px 1px rgba(0,0,0,0.47);
